import Bus from '../../common/js/bus'

export default {
  data() {
    return {
      activeIndex: '应用列表'
    }
  },
  created() {
    this.$nextTick(() => {
      Bus.$on('appdetail', () => {
        this.activeIndex = '应用概述'
      })
      Bus.$on('applist', () => {
        this.activeIndex = '应用列表'
        this.$router.push('/apps')
      })
      if (this.$route.fullPath === '/members') {
        this.activeIndex = '团队管理'
      }
    })

  },
  methods: {
    clickSubItem(data) {
      if (data.index === '应用列表') {
        this.$router.push('/apps')
      }
      if (data.index === '应用概述') {
        Bus.$emit('appSummary')
      }
      if (data.index === '应用设置') {
        Bus.$emit('appSetting')
      }
      if (data.index === '团队管理') {
        this.$router.push('/members')
      }
      if (data.index === 'API文档') {
        let href = `${this.axios.defaults.baseURL}api/swagger`
        window.open(href, '_blank')
      }
      if (data.index === 'Help') {
      }
      if (data.index === 'GitHub') {
      }
      if (data.index === 'About') {
      }
    },
    gotoApiDoc() {

    }
  },
  watch: {
    '$route': (to) => {
    }
  }
}



